import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import yishengList from '@/views/pages/yisheng/list'
import yishengDetail from '@/views/pages/yisheng/formModel'
import yishengAdd from '@/views/pages/yisheng/formAdd'
import shangpinxinxiList from '@/views/pages/shangpinxinxi/list'
import shangpinxinxiDetail from '@/views/pages/shangpinxinxi/formModel'
import shangpinxinxiAdd from '@/views/pages/shangpinxinxi/formAdd'
import yuyuexinxiList from '@/views/pages/yuyuexinxi/list'
import yuyuexinxiDetail from '@/views/pages/yuyuexinxi/formModel'
import yuyuexinxiAdd from '@/views/pages/yuyuexinxi/formAdd'
import newsList from '@/views/pages/news/list'
import storeupList from '@/views/pages/storeup/list'
import cartList from '@/views/pages/shop_order/cart'
import addressList from '@/views/pages/shop_address/list'
import binglidanganList from '@/views/pages/binglidangan/list'
import binglidanganDetail from '@/views/pages/binglidangan/formModel'
import binglidanganAdd from '@/views/pages/binglidangan/formAdd'
import dongwuzhongleiList from '@/views/pages/dongwuzhonglei/list'
import dongwuzhongleiDetail from '@/views/pages/dongwuzhonglei/formModel'
import dongwuzhongleiAdd from '@/views/pages/dongwuzhonglei/formAdd'
import liulangdongwuxinxiList from '@/views/pages/liulangdongwuxinxi/list'
import liulangdongwuxinxiDetail from '@/views/pages/liulangdongwuxinxi/formModel'
import liulangdongwuxinxiAdd from '@/views/pages/liulangdongwuxinxi/formAdd'
import lingyangxinxiList from '@/views/pages/lingyangxinxi/list'
import lingyangxinxiDetail from '@/views/pages/lingyangxinxi/formModel'
import lingyangxinxiAdd from '@/views/pages/lingyangxinxi/formAdd'
import forumList from '@/views/pages/forum/list'
import order_confirm from '@/views/pages/shop_order/confirm'
import ordersList from '@/views/pages/shop_order/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'yishengList',
			component: yishengList
		}, {
			path: 'yishengDetail',
			component: yishengDetail
		}, {
			path: 'yishengAdd',
			component: yishengAdd
		}
		, {
			path: 'shangpinxinxiList',
			component: shangpinxinxiList
		}, {
			path: 'shangpinxinxiDetail',
			component: shangpinxinxiDetail
		}, {
			path: 'shangpinxinxiAdd',
			component: shangpinxinxiAdd
		}
		, {
			path: 'yuyuexinxiList',
			component: yuyuexinxiList
		}, {
			path: 'yuyuexinxiDetail',
			component: yuyuexinxiDetail
		}, {
			path: 'yuyuexinxiAdd',
			component: yuyuexinxiAdd
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'cartList',
			component: cartList
		}
		, {
			path: 'addressList',
			component: addressList
		}
		, {
			path: 'binglidanganList',
			component: binglidanganList
		}, {
			path: 'binglidanganDetail',
			component: binglidanganDetail
		}, {
			path: 'binglidanganAdd',
			component: binglidanganAdd
		}
		, {
			path: 'dongwuzhongleiList',
			component: dongwuzhongleiList
		}, {
			path: 'dongwuzhongleiDetail',
			component: dongwuzhongleiDetail
		}, {
			path: 'dongwuzhongleiAdd',
			component: dongwuzhongleiAdd
		}
		, {
			path: 'liulangdongwuxinxiList',
			component: liulangdongwuxinxiList
		}, {
			path: 'liulangdongwuxinxiDetail',
			component: liulangdongwuxinxiDetail
		}, {
			path: 'liulangdongwuxinxiAdd',
			component: liulangdongwuxinxiAdd
		}
		, {
			path: 'lingyangxinxiList',
			component: lingyangxinxiList
		}, {
			path: 'lingyangxinxiDetail',
			component: lingyangxinxiDetail
		}, {
			path: 'lingyangxinxiAdd',
			component: lingyangxinxiAdd
		}
		, {
			path: 'forumList',
			component: forumList
		}
		, {
			path: 'order_confirm',
			component: order_confirm
		}
		, {
			path: 'ordersList',
			component: ordersList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
